var yzmTimer=60,yzmTime=59,yzmEnable=true;
export default{
	data:function(){
		return {
			pageLoad:false, 
			pageData:{},
			yzm:"",
			telephone:"",
			yzmClass:"",
			yzmStatus:"获取验证码",
		 
		};
	},
	onLoad:function(option){
		this.pageLoad=true;
	},
	 
	methods:{
		downTimer:function(){
			var that=this;
			var it=setInterval(function(){
				yzmEnable=false;
				that.yzmStatus="倒计时"+yzmTime+"秒";
				that.yzmClass="yzmDisable";
				yzmTime--;
				if(yzmTime==0){
					yzmTime=59;
					yzmEnable=true;
					that.yzmClass="";
					that.yzmStatus="获取验证码";
					clearInterval(it);
				}
			},1000);
		},
		getYzm:function(){
				if(!yzmEnable) return false;
				var that=this;
				uni.request({
					url:that.app.apiHost+"?m=register&a=SendSms&ajax=1",
					data:{
						telephone:this.telephone,
						fromapp:that.app.fromapp()
					},
					success:function(res){
						uni.showToast({
							title:res.data.message,
						})
						if(!res.error){
							that.downTimer();
						}
						
					}
				})
		},
		 
		formSubmit:function(e){
			var that=this;
			e.detail.value.password2=e.detail.value.password;
			uni.request({
				url:that.app.apiHost+"?m=register&a=regsave&ajax=1",
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				data:e.detail.value,
				success:function(res){
					var data=res.data;
					if(res.data.error){
						uni.showToast({
							"title":res.data.message
						})
					}else{
						that.app.setAuthCode(data.data.authcode);
						that.app.goHome();
					}
					
				}
			})
			
		}
	},
}
